export default {
  data() {
    return {
      list: {
        add_time: "0",
        addr: "",
        affair_name: "",
        affair_type: "",
        amount: "0.00000000",
        chain: "",
        coin_type: "",
        goods_name: "",
        property_record_id: "",
        provide_num: ""
      }
    };
  },
  filters: {
    formatDate: function(value) {
      let val = value * 1000;
      let date = new Date(val);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
    },
     uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }

  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          property_record_id: that.$route.params.id
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/balance-record-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};



